import * as React from 'react';
import { MenuItem } from '@ali/we-design';
import { MenuItemInnerProps } from '@ali/4ever-component';
import { insertPlaceholder } from '../actions';

const Menu: React.FC<MenuItemInnerProps> = props => {
  const { menuItem, controller } = props;
  const { title, ...rest } = menuItem;
  const { value } = controller;

  const handleClick = () => controller.run('onAction', insertPlaceholder());

  const isDisabled = React.useMemo(() => controller.query('isSelectionInImage') || controller.query('isSelectionInLinkCard'), [value]);

  return (
    <MenuItem {...rest} onClick={handleClick} disabled={isDisabled}>
      {title || ''}
    </MenuItem>
  );
};

export default React.memo(Menu);
